.node--type-portfolio {
  &.node--view-mode-teaser {
    margin-bottom: 19px;
  }
  &.node--view-mode-full {
    .field.field--name-field-site-screenshots {
      .cycle-pager {
        float: left;
        margin-left: -1%;
        margin-right: -1%;
        img {
          width: 23%;
          margin: 10px 1% 0;
        }
      }
    }
  }
}
#pane-contact {
  .intro-text {
    p {
      margin: 0px;
      a:hover {
        text-decoration: underline;
      }
    }
  }
  ul.leadin {
    overflow: hidden;
    margin-top: -5px;
    padding-top: 15px;
    width: 25%;
    display: inline-block;
    li {
      &.email {
        margin:0 13%;
        width:28.6%;
      }
    }
  }
}


